<template>
  <div id="home">
    <ul class="collection with-header">
      <li class="collection-header"><h4>Users</h4></li>
      <li v-for="user in users" v-bind:key="user.id" class="collection-item">
        <div class="chip">{{user.dept}}</div>
        {{user.user_id}}: {{user.name}}
         <router-link class="secondary-content" v-bind:to="{ name: 'view-user', params: { user_id: user.user_id }}"><i class="fa fa-eye"></i></router-link>
      </li>
    </ul>
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
  import db from './firebaseInit'
  export default {
    name: 'home',
    data () {
      return {
        users: [],
        loading: true
      }
    },
    created () {
      db.collection('users').orderBy('dept').get().then((querySnapshot) => {
        this.loading = false
        querySnapshot.forEach((doc) => {
          const data = {
            'id': doc.id,
            'user_id': doc.data().user_id,
            'name': doc.data().name,
            'dept': doc.data().dept,
            'position': doc.data().position
          }
          this.users.push(data)
        })
      })
    }
  }
</script>



